export default "/* .wrapper {\n    text-align: center;\n    padding: 100px;\n    display: flex;\n    justify-content: center;\n} */\n\n*,\n*:before,\n*:after {\n    box-sizing: border-box;\n   \n}\n\n.center {\n    margin: 40px auto;\n}\n\nbody {\n    \n    background-size: cover;\n    font: 14px/20px \"Lato\", Arial, sans-serif;\n    color: #9E9E9E;\n    background-image: url('new.gi.gif');\n   \n}\n\n.clash-card__unit-stats--archer {\n    background: #EE5487;\n}\n\n.clash-card__unit-stats--archer .one-third {\n    border-right: 1px solid #D04976;\n}\n\n.clash-card__unit-stats--wizard {\n    background: #4FACFF;\n}\n\n.clash-card__unit-stats--wizard .one-third {\n    border-right: 1px solid #309eff;\n}\n\n.slide-container {\n    margin: auto;\n    text-align: center;\n}\n\n.wrapper {\n    padding-top: 40px;\n    padding-bottom: 40px;\n}\n\n.wrapper:focus {\n    outline: 0;\n}\n\n.clash-card {\n    background: white;\n    width: 62%;\n    display: inline-block;\n    margin: auto;\n    border-radius: 19px;\n    position: relative;\n    text-align: center;\n    box-shadow: -1px 15px 30px -12px black;\n    z-index: 9999;\n}\n\n.clash-card__image {\n    position: relative;\n    margin-bottom: 35px;\n    border-top-left-radius: 14px;\n    border-top-right-radius: 14px;\n}\n\n.clash-card__image--barbarian img {\n    width: 400px;\n    position: absolute;\n    top: -85px;\n    left: 25px;\n}\n\n.clash-card__level {\n    text-transform: uppercase;\n    font-size: 12px;\n    font-weight: 700;\n    margin-bottom: 3px;\n}\n\n.clash-card__level--barbarian {\n    color: #4FACFF;\n}\n\n.clash-card__unit-name {\n    font-size: 26px;\n    color: black;\n    font-weight: 900;\n    margin-bottom: 5px;\n}\n\n.clash-card__unit-description {\n    padding: 20px;\n    margin-bottom: 10px;\n}\n\n.clash-card__unit-stats--barbarian {\n    background: #EC9B3B;\n}\n\n.clash-card__unit-stats--barbarian .one-third {\n    border-right: 1px solid #BD7C2F;\n}\n\n.clash-card__unit-stats {\n    color: white;\n    font-weight: 700;\n    border-bottom-left-radius: 14px;\n    border-bottom-right-radius: 14px;\n}\n\n.clash-card__unit-stats .one-third {\n    width: 33%;\n    float: left;\n    padding: 20px 15px;\n}\n\n.clash-card__unit-stats sup {\n    position: absolute;\n    bottom: 4px;\n    font-size: 45%;\n    margin-left: 2px;\n}\n\n.clash-card__unit-stats .stat {\n    position: relative;\n    font-size: 24px;\n    margin-bottom: 10px;\n}\n\n.clash-card__unit-stats .stat-value {\n    text-transform: uppercase;\n    font-weight: 400;\n    font-size: 12px;\n}\n\n.clash-card__unit-stats .no-border {\n    border-right: none;\n}\n\n.clearfix:after {\n    visibility: hidden;\n    display: block;\n    font-size: 0;\n    content: \" \";\n    clear: both;\n    height: 0;\n}\n\n.slick-prev {\n    left: 100px;\n    z-index: 999;\n}\n\n.slick-next {\n    right: 100px;\n    z-index: 999;\n}\n\n.card-img {\n    height: 120px !important;\n    top: -75px !important;\n}\n/*# sourceMappingURL=data:application/json;base64, */"